
.container {
  margin: 10px auto;
  text-align: center;
  text-decoration: none;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  padding: 30px;
}
.postCard {
  margin: 10px 10px auto;
  padding: 8px;
  display: inline-flex-box;
  border: 2px solid #f4f4f4;
  text-align: center;
  box-shadow: 1px 2px #ccc;
  border-radius: 10px;
}
.body {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  align-content: center;
}
/* Comment Button Style*/
.btnComment {
  box-sizing: border-box;
  appearance: none;
  background-color: transparent;
  border: 1px solid #5f6ef1;
  border-radius: 0.6em;
  color: #5f6ef1;
  cursor: pointer;
  display: flex;
  align-self: center; 
  line-height: .30;
  margin: 5px;
  padding: 1.2em 2.8em;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
} 
.btnComment:hover,
btnComment:focus {
    color: #fff;
    outline: 0;
  }

.comment {
  transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
}
.comment:hover {
  box-shadow: 0 0 40px 40px #5f6ef1 inset;
}

