/*Clearing Floats*/
.cf:before, .cf:after{
  content:"";
  display:table;
}

.cf:after{
  clear:both;
}

.cf{
  zoom:1;
}    
/* Form wrapper styling */

.update-wrapper {
  width: 80%;
  margin: 50px auto 50px auto;
  border-radius: 10px;
  background: transparent;
  box-shadow: 0 4px 20px -2px #e9e9e9;
}

/* Form text input */

.update-wrapper input {
padding-left: 20px;
  width: 70%;
  height: 20px;
  padding: 10px 5px;
  float: left;   
  /*font: bold 13px 'lucida sans', 'trebuchet MS', 'Tahoma';*/
  text-decoration: none;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  border: 0;
  background: #fff;
  border-radius: 10px;
  border-top-style: none;
}

.update-wrapper input:focus {
  outline: 0;
  background: #fff;
  box-shadow: 0 0 2px rgba(0,0,0,0.8) inset;
}


/* Form submit button */
.update-wrapper button {
  overflow: visible;
  position: relative;
  float: right;
  border: 0;
  padding: 0;
  cursor: pointer;
  height: 40px;
  width: 110px;
  text-decoration: none;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  color: #fff;
  text-transform: uppercase;
  background: #198cff;
  border-radius: 0px 10px 10px 0px;
  text-shadow: 0 -1px 0 rgba(0, 0 ,0, .3);
}  

.update-wrapper button:active,
.update-wrapper button:focus{  
  background: #198cff;
  outline: 0;  
}

.update-wrapper button:focus:before,
.update-wrapper button:active:before{
      border-right-color: #c42f2f;
}     

.update-wrapper button::-moz-focus-inner { /* remove extra button spacing for Mozilla Firefox */
  border: 0;
  padding: 0;
}   

.list {
  list-style-type: square;
  text-align: left;

}
